// src/app/components/Education.jsx

"use client";

import { useState } from 'react';
import { motion } from 'framer-motion';
import { FaGraduationCap, FaMapMarkerAlt, FaCalendarAlt, FaTrophy } from 'react-icons/fa';

const Education = () => {
  const [activeTab, setActiveTab] = useState(0);

  const educationData = [
    {
      id: 1,
      school: "HAMK University of Applied Sciences",
      degree: "Bachelor of Business Administration",
      period: "2022 - 2025",
      location: "H√§meenlinna, Finland",
      description: "Currently pursuing a degree in Business Administration with a focus on digital business and technology management. The program combines business fundamentals with modern digital technologies and management practices.",
      achievements: [
        "Focus on Mathematics and Physics",
        "Active participation in student organizations",
        "Regular academic excellence recognition"
      ]
    },
    {
      id: 2,
      school: "Language Studies in Austria",
      degree: "Language Studies",
      period: "2020 - 2021",
      location: "Austria",
      description: "Intensive language studies focusing on German and cultural immersion.",
      achievements: [
        "Advanced German language proficiency",
        "Cultural exchange participation",
        "International student collaboration"
      ]
    },
    {
      id: 3,
      school: "High School",
      degree: "High School Diploma",
      period: "2017 - 2020",
      location: "Iran",
      description: "Completed high school education with a focus on Mathematics and Physics.",
      achievements: [
        "Top performer in Mathematics",
        "Physics competition participant",
        "Student council member"
      ]
    }
  ];

  return (
    <section id="education" className="py-20 bg-gradient-to-b from-gray-50 to-white dark:from-gray-900 dark:to-gray-800 min-h-screen">
      <div className="container mx-auto px-4">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold mb-4 text-gray-800 dark:text-white">
            Education
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-400">
            My academic journey and achievements
          </p>
        </motion.div>

        {/* Timeline Navigation */}
        <div className="flex flex-wrap justify-center gap-4 mb-12">
          {educationData.map((edu, index) => (
            <motion.button
              key={edu.id}
              onClick={() => setActiveTab(index)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              className={`px-6 py-3 rounded-full transition-all duration-300 flex items-center gap-2 ${
                activeTab === index
                  ? 'bg-primary text-white shadow-lg'
                  : 'bg-gray-100 dark:bg-gray-800 text-gray-600 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700'
              }`}
            >
              <FaGraduationCap className="text-lg" />
              <span>{edu.school}</span>
            </motion.button>
          ))}
        </div>

        {/* Education Details */}
        <motion.div
          key={activeTab}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="max-w-4xl mx-auto"
        >
          <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-xl p-8 transition-all duration-300 hover:shadow-2xl">
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-8">
              <div>
                <h3 className="text-2xl font-bold text-gray-800 dark:text-white mb-2">
                  {educationData[activeTab].degree}
                </h3>
                <p className="text-primary font-medium mb-1 flex items-center gap-2">
                  <FaGraduationCap />
                  {educationData[activeTab].school}
                </p>
              </div>
              <div className="text-right mt-4 md:mt-0 space-y-2">
                <p className="text-gray-600 dark:text-gray-400 flex items-center justify-end gap-2">
                  <FaCalendarAlt />
                  {educationData[activeTab].period}
                </p>
                <p className="text-gray-500 dark:text-gray-500 flex items-center justify-end gap-2">
                  <FaMapMarkerAlt />
                  {educationData[activeTab].location}
                </p>
              </div>
            </div>

            <p className="text-gray-600 dark:text-gray-300 mb-8 text-lg leading-relaxed">
              {educationData[activeTab].description}
            </p>

            <div>
              <h4 className="text-xl font-semibold text-gray-800 dark:text-white mb-4 flex items-center gap-2">
                <FaTrophy />
                Key Achievements
              </h4>
              <ul className="space-y-3">
                {educationData[activeTab].achievements.map((achievement, index) => (
                  <motion.li
                    key={index}
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: index * 0.1 }}
                    className="flex items-center text-gray-600 dark:text-gray-300 bg-gray-50 dark:bg-gray-700 p-3 rounded-lg"
                  >
                    <span className="mr-3 text-primary text-xl">‚Ä¢</span>
                    {achievement}
                  </motion.li>
                ))}
              </ul>
            </div>
          </div>
        </motion.div>
      </div>
    </section>
  );
};

export default Education;